<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">我的账户</div>
            </div>
            <div class="head-line"></div>
            <div class="tag">
                <div class="left">
                    <span class="icon-zh"></span>
                    <div class="desc">我的账号</div>
                </div>
                <div class="right">
                    <div class="value">13326326582</div>
                    <span class="icon-zh"></span>
                </div>
            </div>
            <div class="tag">
                <div class="left">
                    <span class="icon-pj"></span>
                    <div class="desc">剩余评价次数</div>
                </div>
                <div class="right">
                    <div class="value">0次</div>
                    <span class="icon-npj"></span>
                </div>
            </div>
            <div class="tag">
                <div class="left">
                    <span class="icon-mc"></span>
                    <div class="desc">企业名称</div>
                </div>
                <div class="right">
                    <div class="value">未填写</div>
                    <span class="icon-mc" @click="revise('0')"></span>
                </div>
            </div>
            <div class="tag">
                <div class="left">
                    <span class="icon-dz"></span>
                    <div class="desc">企业地址</div>
                </div>
                <div class="right">
                    <div class="value-dz">杭州市萧山区杭州湾启迪路198</div>
                    <span class="icon-dz" @click="revise('1')"></span>
                </div>
            </div>
            <div class="line"></div>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {}
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {
            revise (type) {
                this.$router.push({name: 'ReviseIndex', params: {type: type}})
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     .head-line
      width 7.5rem
      height 0.1rem
      background-color #f9f9f9
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content center
      .title
       align-self center
       color #CBAC86
       set-size(18px)
     .tag
      width 7.5rem
      display flex
      justify-content space-between
      font-size 0
      .left
        display flex
        margin 0.32rem 0 0.27rem 0.3rem
       .icon-zh,.icon-pj,.icon-mc,.icon-dz
        align-self center
        display inline-block
        background-repeat no-repeat
       .icon-zh
        width 0.32rem
        height 0.3rem
        background-size 0.32rem 0.3rem
        background-image url("icon_wodezhanghao.png")
       .icon-pj
        width 0.3rem
        height 0.3rem
        background-size 0.3rem 0.3rem
        background-image url("icon_pingjiashu.png")
       .icon-mc
        width 0.32rem
        height 0.28rem
        background-image url("icon_qiyemingcheng.png")
        background-size 0.32rem 0.28rem
       .icon-dz
        width 0.26rem
        height 0.3rem
        background-size 0.26rem 0.3rem
        background-image url("icon_qiyedizhi.png")
       .desc
        display inline-block
        color #333333
        set-size(16px)
        align-self center
        margin-left 0.21rem
        white-space nowrap
        overflow-y hidden
      .right
       display flex
       margin-right 0.25rem
       .icon-zh,.icon-npj,.icon-mc,.icon-dz
        align-self center
        display inline-block
        width 0.79rem
        height 0.53rem
        background-repeat no-repeat
        background-size 0.79rem 0.53rem
       .icon-zh
        background-image url("btn_tuichu.png")
       .icon-mc,.icon-dz
        background-image url("btn_xiugai.png")
       .value,.value-dz
        display inline-block
        color #999999
        set-size(14px)
        align-self center
        margin-right 0.23rem
       .value-dz
        margin-left 0.42rem
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        max-width 3rem
    .line
     width 7.5rem
     height 0.02rem
     background-color #f9f9f9
</style>



