<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">个人中心</div>
            </div>
            <div class="line"></div>
            <div class="data-wrapper">
                <div class="company">企业名称： 杭州百米贩科技信息公司</div>
                <div class="address">企业地址： 未填写</div>
            </div>
            <div class="line"></div>
            <div class="tag" @click="toaccount">
                <div class="left">
                    <span class="icon-zh"></span>
                    <div class="desc">我的账号</div>
                </div>
                <div class="right">
                    <div class="value">13325623654</div>
                    <span class="icon"></span>
                </div>
            </div>
            <div class="bg-line"></div>
            <div class="tag" @click="tomynotice">
                <div class="left">
                    <span class="icon-gg"></span>
                    <div class="desc">查看公告</div>
                </div>
                <div class="right">
                    <span class="icon"></span>
                </div>
            </div>
            <div class="bg-line"></div>
            <div class="tag" @click="tocomment">
                <div class="left">
                    <span class="icon-pj"></span>
                    <div class="desc">评价</div>
                </div>
                <div class="right">
                    <span class="icon"></span>
                </div>
            </div>
            <div class="line"></div>
            <div class="tag" @click="tocollect">
                <div class="left">
                    <span class="icon-sc"></span>
                    <div class="desc">我的收藏</div>
                </div>
                <div class="right">
                    <span class="icon"></span>
                </div>
            </div>
            <div class="bg-line"></div>
            <div class="tag" @click="tocustomer">
                <div class="left">
                    <span class="icon-kh"></span>
                    <div class="desc">我的客户</div>
                </div>
                <div class="right">
                    <span class="icon"></span>
                </div>
            </div>
            <div class="bg-line"></div>
            <div class="tag">
                <div class="left">
                    <span class="icon-zl"></span>
                    <div class="desc">企业资料</div>
                </div>
                <div class="right">
                    <div class="value">更多资料，请在电脑上完成</div>
                </div>
            </div>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {}
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {
            toaccount () {
                this.$router.push({path: '/account-index'})
            },
            tomynotice () {
                this.$router.push({path: '/myNotice-index'})
            },
            tocomment () {
                this.$router.push({path: '/receive-index'})
            },
            tocollect () {
                this.$router.push({path: '/collect-index'})
            },
            tocustomer () {
                this.$router.push({path: '/confirming-index'})
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .bg-line
      width 7.5rem
      height 0.02rem
      background-color #D7D7D7
     .data-wrapper
      width 7.5rem
      font-size 0
      .company, .address
       display inline-block
       color #333333
       set-size(16px)
       margin-left 0.29rem
       text-overflow ellipsis
       white-space nowrap
      .company
       margin-top 0.48rem
      .address
       margin-top 0.25rem
       margin-bottom 0.23rem
     .tag
      display flex
      justify-content space-between
      .left
       align-self center
       display inline-block
       font-size 0
       margin 0.27rem 0 0.27rem 0.3rem
       .icon-zh, .icon-gg, .icon-pj, .icon-sc, .icon-kh, .icon-zl
        display inline-block
        background-repeat no-repeat
       .icon-zh
        width 0.32rem
        height 0.3rem
        background-size 0.32rem 0.3rem
        background-image url("icon_wodezhanghao.png")
       .icon-gg
        width 0.34rem
        height 0.34rem
        background-size 0.34rem 0.34rem
        background-image url("icon_chakangonggao.png")
       .icon-pj
        width 0.3rem
        height 0.3rem
        background-size 0.3rem 0.3rem
        background-image url("icon_pingjia.png")
       .icon-sc
        width 0.31rem
        height 0.27rem
        background-size 0.31rem 0.27rem
        background-image url("icon_wodeshoucang.png")
       .icon-kh
        width 0.26rem
        height 0.3rem
        background-size 0.26rem 0.3rem
        background-image url("icon_wodekehu.png")
       .icon-zl
        width 0.26rem
        height 0.3rem
        background-size 0.26rem 0.3rem
        background-image url("icon_qiyeziliao.png")
       .desc
        display inline-block
        color #333333
        set-size(16px)
        margin-left 0.21rem
        vertical-align top
      .right
        display inline-block
        font-size 0
        margin-right 0.3rem
        align-self center
        display inline-block
        text-align center
       .value
        color #999999
        set-size(14px)
        display inline-block
        vertical-align bottom
       .icon
        width 0.17rem
        height 0.31rem
        background-repeat no-repeat
        background-size 0.17rem 0.31rem
        background-image url("icon_xiangyou.png")
        display inline-block
        margin-left 0.21rem
</style>



