<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="toast">
                <div class="title">电脑访问获得最佳编辑合同的体验</div>
            </div>
            <div class="content">
                <ul>
                    <li class="item">
                        <div class="title">
                            <div class="name">上海百米贩科技</div>
                            <span class="icon"></span>
                        </div>
                        <div class="desc">
                            <div class="doc">百米贩合同.doc</div>
                            <span class="time">更新时间：2016.11.14</span>
                        </div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {}
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {},
        methods: {}
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .toast
      width 7.5rem
      height 1.31rem
      display flex
      background-color #CBAC86
      justify-content center
      .title
       color #ffffff
       set-size(18px)
       align-self center
     .content
      width 7.5rem
      margin-top 0.1rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        justify-content space-between
        .name
         color #333333
         set-size(14px)
         margin-top 0.53rem
         margin-left 0.29rem
        .icon
         display inline-block
         background-image url("btn_chakankehu.png")
         width 1.41rem
         height 0.53rem
         background-repeat no-repeat
         background-size 1.41rem 0.53rem
         margin-top 0.44rem
         margin-right 0.3rem
       .desc
        display flex
        justify-content space-between
        .doc
         color #999999
         set-size(14px)
         margin-top 0.3rem
         margin-left 0.29rem
        .time
         color #999999
         set-size(14px)
         margin-top 0.22rem
         margin-right 0.32rem
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.42rem auto
</style>



