<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div></div>
                <div class="title">特种混凝土</div>
                <span v-if="!isShow" class="icon" @click="show"></span>
                <span v-else class="icon-h" @click="hide"></span>
            </div>
            <div class="content-wrapper">
                <div class="tabs">
                    <div class="tab-item" @click="tobusinesses">
                        <div :class="[busactive ? 'select-name' : 'name']">企业列表</div>
                        <div :class="[busactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tonotice">
                        <div :class="[noactive ? 'select-name' : 'name']">公告栏</div>
                        <div :class="[noactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <transition enter-active-class="animated slideInRight"
                        leave-active-class="animated slideOutRight">
                <div class="type-wrapper" v-if="isShow">
                    <ul>
                        <li class="type">特种混凝土</li>
                        <li class="type">特种混凝土</li>
                    </ul>
                </div>
            </transition>
            <router-view></router-view>
        </div>
        <navbar selectShow="company"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {
                isShow: false,
                busactive: true,
                noactive: false
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {
            tobusinesses () {
                this.$router.replace({path: '/businesses-index'})
                this.busactive = true
                this.noactive = false
            },
            tonotice () {
                this.$router.replace({path: '/notice-index'})
                this.busactive = false
                this.noactive = true
            },
            hide () {
                this.isShow = false
            },
            show () {
                this.isShow = true
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content space-between
      .title
       align-self center
       set-size(18px)
       color #CBAC86
      .icon-h
       background-image url("icon_quxiao-.png")
      .icon
       background-image url("icon_Hamburger.png")
      .icon,.icon-h
       background-repeat no-repeat
       display inline-block
       align-self center
       margin-right 0.3rem
       width 0.26rem
       height 0.26rem
       background-size 0.26rem 0.26rem
     .content-wrapper
      width 7.5rem
      height 0.81rem
      .tabs
       width 7.5rem
       display flex
       .tab-item
        flex 1
        .name,.select-name
         margin-top 0.24rem
         text-align center
         set-size(18px)
        .name
         color #666666
        .select-name
         color #CBAC86
        .under-line,.select-line
         width 1.61rem
         height 0.05rem
         background-color #CBAC86
         margin 0.18rem auto
        .under-line
         visibility visible
        .select-lin
         visibility hidden
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .type-wrapper
      position fixed
      right 0
      top 0
      bottom 0
      z-index 100
      background-color #212121
      overflow auto
      height auto
      width 2.71rem
      margin-top 0.89rem
      margin-bottom 0.99rem
      .type
       set-size(15px)
       color #ffffff
       padding 0.27rem 0.61rem 0.26rem 0.62rem
       font-weight 600
       border-bottom 0.02rem solid #999999

</style>



