<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div></div>
                <div class="title">工程企业</div>
                <span v-if="!isShow" class="icon" @click="show"></span>
                <span v-else class="icon-h" @click="hide"></span>
            </div>
            <div class="content-wrapper">
                <div class="tabs">
                    <div class="tab-item" @click="tobusinesses">
                        <div :class="[busactive ? 'select-name' : 'name']">企业列表</div>
                        <div :class="[busactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tonotice">
                        <div :class="[noactive ? 'select-name' : 'name']">公告栏</div>
                        <div :class="[noactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                    <div class="tab-item" @click="tointro">
                        <div :class="[introactive ? 'select-name' : 'name']">工艺介绍</div>
                        <div :class="[introactive ? 'under-line' : 'select-lin']"></div>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <transition enter-active-class="animated slideInRight"
                        leave-active-class="animated slideOutRight">
             <div class="type-wrapper" v-if="isShow">
                <ul>
                    <li class="type">特种混凝土</li>
                    <li class="type">特种混凝土</li>
                </ul>
             </div>
            </transition>
            <transition enter-active-class="animated slideInDown"
                        leave-active-class="animated slideOutUp">
                <div class="intro-wrapper" v-if="introactive">
                    <div class="closed">
                        <span class="icon" @click="hideintro"></span>
                    </div>
                    <div class="content">
                        <div class="title">
                            弹性混凝土
                        </div>
                        <div class="select-wrapper">
                            <div class="desc">
                                弹性体泛指在除去外力后能恢复原状的材料，
                                然而具有弹性的材料并不一定是弹性体。
                                弹性体只是在弱应力下形变显著，
                                应力松弛后能迅速恢复到接近原有状态和尺寸的高分子材料 。
                                热塑性弹性体是弹性体一类重要组成，两种概念不可混为一谈。
                                建筑结构中理想的弹性材料是没有的所谓弹性就是:材料受力会变形,
                                释放后会完全恢复到原来的形状. 钢筋和
                            </div>
                            <div class="next-wrapper">
                                <div class="left">
                                    上一条
                                </div>
                                <div class="right">
                                    下一条
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
            <router-view></router-view>
        </div>
        <navbar selectShow="company"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {
                isShow: false,
                busactive: true,
                noactive: false,
                introactive: false
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {
            tobusinesses () {
                this.$router.replace({path: '/eng-businesses-index'})
                this.busactive = true
                this.noactive = false
                this.introactive = false
            },
            tonotice () {
                this.$router.replace({path: '/eng-notice-index'})
                this.busactive = false
                this.noactive = true
                this.introactive = false
            },
            tointro () {
                this.busactive = false
                this.noactive = false
                this.introactive = true
            },
            hide () {
                this.isShow = false
            },
            show () {
                this.isShow = true
            },
            hideintro () {
                this.introactive = false
                this.tobusinesses()
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content space-between
      .title
       align-self center
       set-size(18px)
       color #CBAC86
      .icon-h
       background-image url("icon_quxiao-.png")
      .icon
       background-image url("icon_Hamburger.png")
      .icon,.icon-h
       background-repeat no-repeat
       display inline-block
       align-self center
       margin-right 0.3rem
       width 0.26rem
       height 0.26rem
       background-size 0.26rem 0.26rem
     .content-wrapper
      width 7.5rem
      height 0.81rem
      .tabs
       width 7.5rem
       display flex
       .tab-item
        flex 1
        .name,.select-name
         margin-top 0.24rem
         text-align center
         set-size(18px)
        .name
         color #666666
        .select-name
         color #CBAC86
        .under-line,.select-line
         width 1.61rem
         height 0.05rem
         background-color #CBAC86
         margin 0.18rem auto
        .under-line
         visibility visible
        .select-lin
         visibility hidden
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .type-wrapper
      position fixed
      right 0
      top 0
      bottom 0
      z-index 100
      background-color #212121
      overflow auto
      height auto
      width 2.71rem
      margin-top 0.89rem
      margin-bottom 0.99rem
      .type
       set-size(15px)
       color #ffffff
       padding 0.27rem 0.61rem 0.26rem 0.62rem
       font-weight 600
       border-bottom 0.02rem solid #999999
     .intro-wrapper
      width 7.5rem
      height 100%
      position fixed
      top 0
      bottom 0
      overflow auto
      background-color rgba(7, 17, 27, 0.4)
      z-index 100
      font-size 0
      padding-top 0.6rem
      .closed
       width 7.5rem
       .icon
        width 0.61rem
        height 0.61rem
        background-repeat no-repeat
        background-size 0.61rem 0.61rem
        background-image url("icon_quxiao.png")
        display inline-block
        float right
        margin-right 0.6rem
      .content
       width 7.5rem
       display flex
       justify-content center
       flex-direction column
       .title
        width 4.41rem
        height 1.12rem
        background-repeat no-repeat
        background-size 4.41rem 1.12rem
        background-image url("pic_chengweiyouzhihehuoren.png")
        set-size(20px)
        color #FFFFFF
        align-self center
        text-align center
        line-height 1.12rem
       .select-wrapper
        width 5.11rem
        height 7.41rem
        background-repeat no-repeat
        background-size 5.11rem 7.41rem
        align-self center
        background-color #ffffff
        .next-wrapper
         position relative
         width 5.11rem
         height 1.05rem
         display flex
         justify-content center
         .left,.right
          set-size(15px)
          flex 1
          align-self center
          text-align center
          margin 0.12rem 0.37rem 0.11rem 0.37rem
          padding 0.12rem 0.37rem 0.11rem 0.37rem
          border 0.02rem solid #999999
          white-space nowrap
        .desc
         set-size(14px)
         color #666666
         align-self center
         overflow scroll
         height 5.42rem
         text-align center
         padding 0.47rem 0.4rem 0.47rem 0.36rem
</style>



