<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">
                    {{title}}
                </div>
            </div>
            <div class="input-wrapper">
                <input class="text" :placeholder="holder">
            </div>
            <div class="hint">
                {{hint}}
            </div>
            <div class="btn-wrapper">
                保存
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                title: '修改企业',
                holder: '未填写',
                hint: '*请仔细填写相关的企业信息'
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            const type = this.$route.params.type
            if (type === '0') {
                this.title = '修改企业'
                this.holder = '未填写'
                this.hint = '*请仔细填写相关的企业信息'
            } else {
                this.title = '修改地址'
                this.holder = '详细地址'
                this.hint = '*请仔细填写相关的地址信息'
            }
        },
        components: {},
        methods: {}
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     height 100%
     background-color #f9f9f9
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content center
      .title
       align-self center
       color #CBAC86
       set-size(18px)
     .input-wrapper
      width 7.5rem
      .text
       width 6.9rem
       margin 0.45rem 0.3rem 0.24rem 0.17rem
       background-color #ffffff
       height 0.88rem
       padding-left 0.17rem
     .hint
      color #999999
      set-size(14px)
      width 7.5rem
      text-align center
     .btn-wrapper
      width 6.91rem
      margin 0.48rem 0.3rem 0 0.3rem
      height 0.81rem
      background-color #DAC098
      color #ffffff
      set-size(18px)
      text-align center
      line-height 0.81rem
      box-shadow 0.1rem 0.1rem 0.05rem #ece6dc
      border-radius 0.1rem
</style>



