<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="toast">
                <div class="title">电脑访问获得最佳编辑合同的体验</div>
            </div>
            <div class="content">
                <ul>
                    <li class="item">
                        <div class="title">
                            <div class="name">温州九阳防水隔热工程有限公司</div>
                            <div class="time">201.03.16</div>
                        </div>
                        <div class="desc">等待双方确认关系</div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {}
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {},
        methods: {}
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      height 0.09rem
      background-color #f9f9f9
     .toast
      width 7.5rem
      height 1.31rem
      display flex
      background-color #CBAC86
      justify-content center
      .title
       color #ffffff
       set-size(18px)
       align-self center
     .content
      width 7.5rem
      margin-top 0.09rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        justify-content space-between
        .name
         color #333333
         set-size(16px)
         margin-top 0.45rem
         margin-left 0.3rem
         white-space nowrap
         overflow-y hidden
        .time
         color #999999
         set-size(14px)
         margin-top 0.51rem
         margin-right 0.31rem
       .desc
        color #999999
        set-size(14px)
        margin 0.24rem 0 0.41rem 0.3rem
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0 auto
</style>



