<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">我的收藏</div>
            </div>
            <div class="content">
                <ul>
                    <li class="item">
                        <div class="title">
                            <div class="name">温州九阳防水隔热工程有限公司</div>
                            <div class="time">2017.03.16</div>
                        </div>
                        <div class="step">
                            <span class="cancel"></span>
                            <span class="check"></span>
                        </div>
                        <div class="line"></div>
                    </li>
                </ul>
            </div>
        </div>
        <navbar selectShow="personal"></navbar>
    </div>
</template>
<script>
    import NavBar from '../navbar/navbar.vue'
    export default {
        data () {
            return {}
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {
            navbar: NavBar
        },
        methods: {}
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     .head
      width 7.5rem
      height 0.89rem
      background-color #141014
      display flex
      justify-content center
      .title
       align-self center
       color #CBAC86
       set-size(18px)
     .content
      width 7.5rem
      margin-top 0.26rem
      .item
       width 7.5rem
       .title
        display flex
        justify-content space-between
        font-size 0
        .name
         set-size(14px)
         color #333333
         margin-top 0.3rem
         margin-left 0.3rem
         white-space nowrap
         overflow-y hidden
        .time
         set-size(14px)
         color #999999
         margin-top 0.32rem
         margin-right 0.3rem
       .step
        display flex
        flex-direction row-reverse
        .cancel,.check
         width 1.41rem
         height 0.53rem
         background-repeat no-repeat
         background-size 1.41rem 0.53rem
         margin-top 0.32rem
        .check
         background-image url("btn_quxiaoshoucang.png")
        .cancel
         background-image url("btn_chakanqiye.png")
         margin-left 0.3rem
         margin-right 0.3rem
       .line
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.29rem auto
</style>



