<template>
    <div>
        <div class="wrapper">
            <div class="line"></div>
            <div class="content">
                <ul>
                    <li class="item">
                        <div class="title">
                            <div class="name">
                                用户名：15142635689
                            </div>
                            <div class="time">
                                2017.03.16
                            </div>
                        </div>
                        <div class="desc">
                            农田水利主要是灌溉和排水，兼及中小型河道整治，
                            塘坝水库及圩垸建设，低产田水利土壤改良，
                            农田水土保持、土地整治以及农牧供水等。
                            由于各农业地区的自然条件和生产方式千差万
                        </div>
                        <div class="step">
                            <span class="icon-del"></span>
                            <span class="icon-check"></span>
                        </div>
                        <div class="line-new"></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {Toast} from 'mint-ui'
    export default {
        data () {
            return {
                user_id: '',
                api: ''
            }
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
            this.user_id = this.$store.state.user_id
            this.api = this.$store.state.api
            console.log(this.user_id)
            this.getsend()
            if (this.user_id !== '') {
                this.getsend()
            } else {
                this.$router.replace({path: '/login-index'})
            }
        },
        components: {},
        methods: {
            getsend () {
                this.$http.post(this.api + '/index/my/post', {user_id: this.user_id}).then(function (response) {
                    console.log(JSON.stringify(response))
                }, function (response) {
                    Toast({
                        message: 'error',
                        position: 'bottom',
                        duration: 3000
                    })
                })
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../../common/stylus/base.styl"
    .wrapper
     width 7.5rem
     .line
      width 7.5rem
      background-color #f9f9f9
      height 0.05rem
     .content
      width 7.5rem
      margin-top 0.18rem
      .item
       width 7.5rem
       font-size 0
       .title
        display flex
        margin-top 0.3rem
        justify-content space-between
        .name
         set-size(14px)
         color #CBAC86
         margin-left 0.3rem
        .time
         color #CBAC86
         set-size(10px)
         margin-right 0.35rem
       .desc
         color #666666
         set-size(13px)
         margin 0.14rem 0.44rem 0 0.3rem
       .line-new
        width 6.91rem
        height 0.02rem
        background-image url("line.png")
        margin 0.22rem auto
       .step
        width 7.5rem
        display flex
        flex-direction row-reverse
        margin-top 0.21rem
        .icon-del,.icon-check
         display inline-block
         width 0.79rem
         height 0.53rem
         background-repeat no-repeat
         background-size 0.79rem 0.53rem
        .icon-del
         background-image url("btn_xiugai.png")
         margin-left 0.35rem
         margin-right 0.3rem
        .icon-check
         background-image url("btn_shanchu.png")
</style>



