<template>
    <div>
        <div class="wrapper">
            <div class="head">
                <div class="title">企业列表</div>
            </div>
            <div class="lists">
                <ul>
                    <li class="list">
                        <div class="title">
                            <div class="name">浙江大成建筑有限公司</div>
                            <span class="icon"></span>
                        </div>
                        <div class="desc">泡沫沥青就地冷再生、深孔爆破、深孔爆破、泡沫沥青就地冷再</div>
                        <div class="detail" @click="tobusiness">
                            <span class="icon"></span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {}
        },
        created () {
            document.documentElement.style.fontSize =
                    document.documentElement.clientWidth / 7.5 + 'px'
        },
        components: {},
        methods: {
            tobusiness (id) {
                this.$router.push({path: '/business-index'})
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
    @import "../../common/stylus/base.styl"
    .wrapper
     position absolute
     bottom 0.99rem
     top 0
     overflow-y scroll
     -webkit-overflow-scrolling touch
     width 7.5rem
     .head
      width 7.5rem
      height 0.89rem
      display flex
      justify-content center
      background-color #141014
      .title
       color #CBAC86
       align-self center
       set-size(18px)
     .lists
      width 7.5rem
      .list
       width 7.5rem
       font-size 0
       position relative
       .title
        margin-left 0.29rem
        margin-top 0.35rem
        display flex
        .name
         display inline-block
         color #333333
         font-weight 600
         set-size(15px)
         white-space nowrap
         align-self center
        .icon
         align-self center
         display inline-block
         width 0.4rem
         height 0.47rem
         background-size 0.4rem 0.47rem
         background-repeat no-repeat
         background-image url("pic_renzheng.png")
         margin-left 0.1rem
       .desc
        margin-left 0.29rem
        set-size(12px)
        color #999999
        margin-top 0.23rem
        white-space nowrap
        margin-bottom 0.23rem
        white-space nowrap
        overflow-y hidden
        text-overflow ellipsis
        margin-right 0.5rem
       .detail
        position absolute
        top 0
        right 0
        .icon
         display inline-block
         width 1.81rem
         height 0.51rem
         background-size 1.81rem 0.51rem
         background-repeat no-repeat
         background-image url("btn_chakanxiangqing.png")
         margin-right 0.31rem
</style>



